* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI',
        Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #dce3eb;
    height: 100%;
}

html,
body,

#potato,
    .container{
        height: 100%;
        display: flex;
        justify-content: center;
    }
        
    .loader{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 300;
    }

    .movies{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 50px;
        width: 80%;
        padding-top: 70px;
        align-items: flex-start;
    }

    .movies .movie{
        width: 45%;
        margin-bottom: 70px;
        background-color: white;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        font-weight: 300px;
        padding: 20px;
        color: #adaeb9;
        border-radius: 5px;
        box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);  
        
    }
    
    .movie img{
        top: -50px;
        width:100%;
        max-width: 150px;
        margin-right: 30px;
        position: relative;
        box-shadow:0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    }

    .movie .movie__title,
    .movie .movie__year {
        font-weight: 500;
        margin: 0;
    }

    .movie .movie__title{
        margin-bottom: 5px;
        font-size: 24px;
        color: #2c2c2c;
        background-color: #86ce9e;
        
    }

    .movie .movie__genres {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        margin: 5px 0px;

        
    }

    .movie__genres li{
        margin-right: 10px;
        font-size: 14px;
    }
    